<template>
    <div class="information">
      <div class="avatar">
        <v-avatar
            color="grey lighten-2"
            size="70"
        ><span class="black--text text-h5">{{avatarText}}</span>
        </v-avatar>
      </div>
      <div class="infor">
        <span class="name">{{userInformation.firstName +' '+userInformation.lastName}}</span>
        <span class="inoff">Total notes: {{totalNote}}</span>
      </div>
    </div>
</template>

<script>
import service from '../../service/service'
export default {
  data(){
    return {
      userInformation:{
        firstName: '',
        lastName: '',
        userName: '',
      },
      avatarText:'',
      totalNote: '',
    }
  },
  methods:{
    getAvatarText(){
      this.avatarText = this.userInformation.firstName.charAt(0) + this.userInformation.lastName.charAt(0);
    },
    getUserInformation(){
      this.userInformation = service.getUser();
      service.getAllNotesAsync().then(res => this.totalNote = res.length);
    }
  },
  mounted(){
    this.getUserInformation();
    this.getAvatarText();
  }
}
</script>


<style scoped>
.information{
  margin-top: 110px;
  background-color: rgb(53, 53, 53);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.avatar{
  margin: 15px 18px 15px 18px;
}
.infor{
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.name{
  font-size:22px
}
</style>